import React, { Component, Fragment } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import AppHeader from '../common/AppHeader';
import CurrentBidding from './CurrentBidding';
import BidDetails from './BidsDetails';
import { fetchBidDetails } from '../../actions/BidActions';

class Bids extends Component {
  constructor(props) {
    super(props);
    this.state = {
      itemId: this.props.match.params.bidItemId
        ? Number(this.props.match.params.bidItemId)
        : 0
    };
  }

  componentDidUpdate(prevProps) {}
  render() {
    const { classes, auctionSchedule, fetchBidDetails } = this.props;
    return (
      <Fragment>
        <AppHeader />
        <div className={classes.wrapper}>
          {this.state.bidItemId === 0 ? (
            <CurrentBidding auctionSchedule={auctionSchedule} />
          ) : (
            <BidDetails
              fetchBidDetails={fetchBidDetails}
              itemId={this.state.itemId}
            />
          )}
        </div>
      </Fragment>
    );
  }
}

const styles = () => ({
  wrapper: {
    display: 'flex',
    padding: '70px 50px 50px 50px',
    flexDirection: 'column'
  }
});

const mapStateToProps = state => {
  return {
    username: state.user.username
  };
};

const mapDispatchToProps = dispatch => {
  return bindActionCreators(
    {
      fetchBidDetails
    },
    dispatch
  );
};

export default withStyles(styles)(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Bids)
);
