import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Table from '@material-ui/core/Table';
import TableRow from '@material-ui/core/TableRow';
import TableHead from '@material-ui/core/TableHead';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import RadioGroup from '@material-ui/core/RadioGroup';
import Radio from '@material-ui/core/Radio';
import FormLabel from '@material-ui/core/FormLabel';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Visibility from '@material-ui/icons/Visibility';
import ItemDetailsDialog from '../common/ItemDetailsDialog';
import _ from 'lodash';
import moment from 'moment';
import { fetchAllAuctionItems } from '../../actions/AuctionActions';

class AuctionList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
      startDate: '',
      endDate: '',
      selection: 'date',
      search: '',
      selectedItemId: 0
    };
  }

  handleChange = field => event => {
    this.setState({ updateSuccessful: false, updateSuccessText: '' });
    this.setState({ [field]: event.target.value });
    if (_.eq(field, 'startDate') && _.eq(this.state.selection, 'date')) {
      this.setState({
        endDate: event.target.value
      });
    }
  };

  handleDateSelectionChange = event => {
    this.setState({ selection: event.target.value });
  };

  handleSubmit = event => {
    event.preventDefault();
    const { pageIndex, pageSize, order, orderBy } = this.props;
    const { startDate, endDate } = this.state;
    const dateObj = {
      startDate: this.convertToEpoch(startDate),
      endDate: this.convertToEpoch(endDate),
      items: true
    };
    this.props.fetchAllAuctionItems(
      dateObj,
      pageIndex,
      pageSize,
      order,
      orderBy
    );
  };

  convertToEpoch = date => {
    return new Date(`${date}T00:00Z`).getTime();
  };

  handleViewItem = itemId => {
    console.log('item id: ', itemId);
    this.setState({
      selectedItemId: itemId
    });
    this.handleOpen();
  };

  handleOpen = () => {
    this.setState({
      open: true
    });
  };

  handleClose = () => {
    this.setState({
      open: false
    });
  };

  render() {
    const { classes, tableHeaders, auctionItems } = this.props;
    const noItems =
      'There are no items available for auction for this date (range).';
    return (
      <div className={classes.wrapper}>
        <ItemDetailsDialog
          itemId={this.state.selectedItemId}
          onClose={this.handleClose}
        />
        <div className={classes.titleBarWrapper}>
          <Typography
            variant="h3"
            align="left"
            classes={{ root: classes.title }}
          >
            {'List of items up for auction'}
          </Typography>
        </div>
        <TextField
          id="search"
          type="text"
          placeholder={'Search...'}
          value={this.state.search}
          required
          onChange={this.handleChange('search')}
        />
        <form onSubmit={this.handleSubmit} className={classes.scheduleForm}>
          <FormControl className={classes.formControl}>
            <FormLabel className={classes.dateSelectorLabel}>Select</FormLabel>
            <RadioGroup
              aria-label="date-selector"
              name="dateSelector"
              className={classes.group}
              value={this.state.selection}
              onChange={event => this.handleDateSelectionChange(event)}
            >
              <FormControlLabel
                className={classes.radioControl}
                value="date"
                control={<Radio />}
                label="A Date"
              />
              <FormControlLabel
                className={classes.radioControl}
                value="dateRange"
                control={<Radio />}
                label={'Date Range'}
              />
            </RadioGroup>
          </FormControl>
          <TextField
            id="startDate"
            type="date"
            label={this.state.selection === 'date' ? 'Date' : 'Start Date'}
            value={this.state.startDate}
            required
            className={classes.textField}
            onChange={this.handleChange('startDate')}
          />
          {_.eq(this.state.selection, 'dateRange') && (
            <TextField
              id="endDate"
              type="date"
              label="End Date"
              value={this.state.startDate}
              required
              className={classes.textField}
              onChange={this.handleChange('endDate')}
            />
          )}
          <div className={classes.btnWrapper}>
            <Button
              variant="contained"
              color={'primary'}
              className={classes.saveBtn}
              type={'submit'}
            >
              {'Show All Items'}
            </Button>
          </div>
        </form>
        <Card className={classes.card}>
          <CardContent className={classes.cardContent}>
            <Table>
              <TableHead>
                <TableRow>
                  {tableHeaders.map(header => {
                    return (
                      <TableCell
                        key={`${header.id}`}
                        padding="dense"
                        numeric={header.numeric}
                        className={classes.headerCell}
                      >
                        {header.label}
                      </TableCell>
                    );
                  })}
                </TableRow>
              </TableHead>
              <TableBody>
                {!_.isEmpty(auctionItems) ? (
                  auctionItems.map((auction, index) => {
                    return (
                      <TableRow
                        hover
                        key={`${auction.id}-${index}`}
                        className={classes.tableRow}
                      >
                        <TableCell
                          classes={{ root: classes.tableCell }}
                          padding="dense"
                        >
                          {auction.itemId}
                        </TableCell>
                        <TableCell
                          classes={{ root: classes.tableCell }}
                          padding="dense"
                        >
                          {moment
                            .utc(auction.auctionDate)
                            .format('MM-DD-YYYY HH:mm:ss A')}
                        </TableCell>
                        <TableCell
                          classes={{ root: classes.tableCell }}
                          padding="dense"
                        >
                          {moment.utc(auction.startTime).format('HH:mm:ss A')}
                        </TableCell>
                        <TableCell
                          classes={{ root: classes.tableCell }}
                          padding="dense"
                        >
                          {moment.utc(auction.endTime).format('HH:mm:ss A')}
                        </TableCell>
                        <TableCell
                          classes={{ root: classes.tableCell }}
                          padding="dense"
                          onClick={() => this.handleViewItem(auction.itemId)}
                        >
                          <Visibility />
                        </TableCell>
                      </TableRow>
                    );
                  })
                ) : (
                  <TableRow className={classes.tableRow}>
                    <TableCell
                      className={classes.tableCell}
                      padding="dense"
                      colSpan={tableHeaders.length + 1}
                    >
                      <span className={classes.emptyTableText}>{noItems}</span>
                    </TableCell>
                  </TableRow>
                )}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      </div>
    );
  }
}

const styles = () => ({
  wrapper: {
    display: 'flex',
    padding: '70px 50px 50px 50px',
    flexDirection: 'column'
  },
  scheduleForm: {
    display: 'flex',
    flexDirection: 'column',
    flexWrap: 'wrap',
    padding: '10px',
    justifyContent: 'flex-start'
  },
  dateSelectorLabel: {
    fontSize: 12,
    lineHeight: 5
  },
  slotsWrapper: {
    display: 'flex',
    flexDirection: 'row',
    flexWrap: 'wrap',
    padding: '10px',
    justifyContent: 'flex-start'
  },
  radioControl: {
    transform: 'scale(.8)'
  },
  group: {
    flexDirection: 'row',
    marginLeft: 20
  },
  textField: {
    width: '30vw'
  },
  saveBtn: {
    margin: '15px 5px 15px 0',
    padding: '5px',
    textTransform: 'none'
  },
  formControl: {
    marginTop: 16,
    display: 'flex',
    flexDirection: 'row'
  },
  titleBarWrapper: {
    display: 'flex',
    flexDirection: 'row',
    marginTop: 20,
    justifyContent: 'space-between'
  },
  title: {
    fontFamily: 'Lucida Console, Monaco, monospace'
  },
  card: {
    background: '#FFFFFF',
    margin: '25px 0',
    overflowX: 'auto'
  },
  cardContent: {
    paddingTop: 0,
    paddingLeft: 16,
    paddingRight: 16,
    '&:last-child': {
      paddingBottom: 0
    }
  },
  headerCell: {
    fontWeight: 'bold',
    fontSize: '13px',
    borderBottomWidth: '2px',
    borderBottomColor: '#3f51b5'
  },
  tableCell: {
    color: '#888',
    borderBottomColor: '#3f51b5'
  },
  tableRow: {
    height: 40
  },
  emptyTableText: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '5vh'
  }
});

const mapStateToProps = state => {
  return {
    username: state.user.username,
    tableHeaders: state.auctions.tableHeaders || [],
    auctionItems: state.auctions.auctionItems || [],
    pageIndex: state.auctions.pageIndex,
    pageSize: state.auctions.pageSize,
    order: state.auctions.order,
    orderBy: state.auctions.orderBy,
    count: state.auctions.count
  };
};

const mapDispatchToProps = dispatch => {
  return bindActionCreators(
    {
      fetchAllAuctionItems
    },
    dispatch
  );
};

export default withStyles(styles)(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(AuctionList)
);
